import Vue from 'vue'
import Router from 'vue-router'
import Cars from '@/pages/cars/cars'
import Info from '@/pages/info'
import Web from '@/pages/web/web'
import NO1 from '@/pages/web/web_pages/NO1'

Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/',
        component: Info,
        children:[{
                path: '/',
                component:Web,
                name:'web'
            },{
                path:'/cars',
                name:'cars',
                component:Cars
            }
        ]
    },{
        path: '/no1',
        name: 'no1',
        component: NO1 
    }
  ]
})



